/* Minimal CSS Reset */

html {
    box-sizing: border-box;
    font-size: 12px;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
    margin: 0;
    padding: 0;
    font-weight: normal;
}

ol,
ul {
    list-style: none;
}

img {
    max-width: 100%;
    height: auto;
}
.layout{
    display: flex;
    flex-direction: row;
    min-height: 100vh;
    justify-content: center;
    padding: 0 20px;

    align-items: center;
}
.input-box .output-box{
    display:flex;
    flex-direction: row;
    align-items: center;
    padding: 20px;
    background: #282c34;
}
.test{
    margin:0.5em;
    height:65em;
    width: 52em;
    background: #282c34;
}
